.product-details {
  max-width: 1200px;
  margin: 20px auto;
  padding: 20px;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 50%, #f5f5f5 100%);
  box-sizing: border-box;
  position: relative;
  margin-bottom: 50px;
}

.product-details::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: radial-gradient(
      circle at 15% 25%,
      rgba(231, 76, 60, 0.03) 0%,
      transparent 40%
    ),
    radial-gradient(
      circle at 85% 75%,
      rgba(0, 123, 255, 0.03) 0%,
      transparent 40%
    );
  pointer-events: none;
  z-index: 0;
}

.product-details-path {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #294f25;
  margin-bottom: 30px;
  padding: 12px 20px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.2);
  letter-spacing: 1px;
  text-transform: uppercase;
  position: relative;
  z-index: 1;
}

.product-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  align-items: flex-start;
  position: relative;
  z-index: 1;
}

.product-images-section {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 20px;
  align-items: flex-start;
}

.product-image-list {
  display: flex;
  gap: 12px;
  width: 100px;
  margin-bottom: 50px;
}

.product-image-list img {
  width: 100px;
  height: 120px;
  object-fit: cover;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  border: 2px solid transparent;
}

.product-image-list img:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  border-color: #e74c3c;
}

.product-image-list img.active {
  border-color: #e74c3c;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(231, 76, 60, 0.3);
}

.product-MainImage {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.product-MainImage img {
  width: 100%;
  max-width: 450px;
  height: 500px;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1), 0 10px 20px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
}

.product-MainImage img:hover {
  transform: translateY(-5px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15), 0 15px 25px rgba(0, 0, 0, 0.08);
}

.product-details-right {
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1), 0 10px 20px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  height: fit-content;
}

.product-details-right:hover {
  transform: translateY(-2px);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15), 0 15px 25px rgba(0, 0, 0, 0.08);
}

.product-details-right h1 {
  font-family: "Playfair Display", serif;
  font-size: 2.2rem;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 15px;
  background: linear-gradient(135deg, #2c3e50 0%, #34495e 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -0.5px;
  line-height: 1.2;
}

.product-rating {
  display: flex;
  align-items: center;
  gap: 5px;
  margin-bottom: 15px;
  padding: 8px 0;
}

.product-rating img {
  width: 18px;
  height: 18px;
  transition: transform 0.2s ease;
}

.product-rating img:hover {
  transform: scale(1.1);
}

.product-price {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  gap: 15px;
}

.product-price s {
  color: #95a5a6;
  font-size: 1.2rem;
  font-weight: 500;
}

.product-price::after {
  content: attr(data-new-price);
  color: #e74c3c;
  font-weight: 800;
}

.product-description {
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
  line-height: 1.6;
  color: #555;
  margin-bottom: 25px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(231, 76, 60, 0.1);
}

.product-size {
  margin-bottom: 25px;
}

.product-size p {
  font-family: "Montserrat", sans-serif;
}

.product-size h3 {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.size-options {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.product-size button {
  padding: 10px 16px;
  border: 2px solid #e1e8ed;
  background: rgba(255, 255, 255, 0.8);
  color: #2c3e50;
  border-radius: 8px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 45px;
}

.product-size button:hover {
  border-color: #e74c3c;
  background: rgba(231, 76, 60, 0.1);
  color: #e74c3c;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(231, 76, 60, 0.2);
}

.product-size button.selected {
  border-color: #e74c3c;
  background: #e74c3c;
  color: white;
  transform: translateY(-1px);
  box-shadow: 0 6px 20px rgba(231, 76, 60, 0.3);
}

.Cart-Submit {
  margin: 25px 0;
}

.Cart-Submit button {
  background: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
  color: white;
  border: none;
  padding: 15px 0;
  border-radius: 50px;
  font-size: 15px;
  font-weight: 700;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  transition: all 0.4s ease;
  box-shadow: 0 8px 25px rgba(231, 76, 60, 0.3);
  position: relative;
  overflow: hidden;
  width: 100%;
}

.Cart-Submit button::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.3),
    transparent
  );
  transition: left 0.6s ease;
}

.Cart-Submit button:hover::before {
  left: 100%;
}

.Cart-Submit button:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 30px rgba(231, 76, 60, 0.4);
  background: linear-gradient(135deg, #c0392b 0%, #a93226 100%);
}

.Cart-Submit button:active {
  transform: translateY(-1px);
}

.product-meta {
  margin-top: 25px;
  padding-top: 20px;
  border-top: 1px solid rgba(231, 76, 60, 0.1);
}

.product-meta p {
  font-family: "Montserrat", sans-serif;
  font-size: 13px;
  color: #666;
  margin: 8px 0;
  font-weight: 500;
  letter-spacing: 0.3px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product-meta p strong {
  color: #2c3e50;
  font-weight: 600;
}

/* Quantity Selector */
.quantity-selector {
  display: flex;
  align-items: center;
  gap: 15px;
  margin: 20px 0;
}

.quantity-selector h4 {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 600;
  color: #2c3e50;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.quantity-controls {
  display: flex;
  align-items: center;
  border: 2px solid #e1e8ed;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.8);
}

.quantity-controls button {
  background: none;
  border: none;
  padding: 10px 12px;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  color: #2c3e50;
  transition: all 0.3s ease;
}

.quantity-controls button:hover {
  background: rgba(231, 76, 60, 0.1);
  color: #e74c3c;
}

.quantity-controls span {
  padding: 10px 15px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  color: #2c3e50;
  min-width: 40px;
  text-align: center;
  border-left: 1px solid #e1e8ed;
  border-right: 1px solid #e1e8ed;
}

/* Responsive Design */
@media (max-width: 768px) {
  .product-container {
    grid-template-columns: 1fr;
    gap: 25px;
  }

  .product-images-section {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .product-image-list {
    flex-direction: row;
    overflow-x: auto;
    padding-bottom: 10px;
    width: 100%;
  }

  .product-image-list img {
    flex-shrink: 0;
    width: 80px;
    height: 100px;
  }

  .product-MainImage img {
    height: 400px;
    max-width: 100%;
  }

  .product-details-right {
    padding: 25px;
  }

  .product-details-right h1 {
    font-size: 1.8rem;
  }

  .product-price {
    font-size: 1.4rem;
  }
}

@media (max-width: 480px) {
  .product-details {
    padding: 15px;
  }

  .product-details-path {
    font-size: 12px;
    padding: 10px 15px;
    margin-bottom: 20px;
  }

  .product-container {
    gap: 20px;
  }

  .product-MainImage img {
    height: 350px;
  }

  .product-details-right {
    padding: 20px;
  }

  .product-details-right h1 {
    font-size: 1.6rem;
    margin-bottom: 12px;
  }

  .product-price {
    font-size: 1.2rem;
  }

  .size-options {
    gap: 8px;
  }

  .product-size button {
    padding: 8px 12px;
    font-size: 13px;
    min-width: 40px;
  }

  .Cart-Submit button {
    padding: 12px 0;
    font-size: 14px;
  }

  .quantity-controls button,
  .quantity-controls span {
    padding: 8px 10px;
  }

  .quantity-controls span {
    min-width: 35px;
  }
}

/* Loading states */
.product-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 400px;
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  color: #666;
}

.product-error {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 400px;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  color: #e74c3c;
}

.product-error h2 {
  font-size: 24px;
  margin-bottom: 15px;
}

.product-error p {
  font-size: 16px;
  color: #666;
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.product-details-right {
  animation: fadeInUp 0.6s ease-out;
}

.product-images-section {
  animation: fadeInUp 0.6s ease-out 0.2s both;
}
